export default {
	props:{
		items:Array
	},
	onLoad() {
		
	},
	methods: {
		doAttention () {
			uni.showToast({
				title: '关注成功',
				duration: 1500
			});
		},
		gotoDetail(item){
			uni.navigateTo({ //这里页面二次跳转后页面不刷新 +'&num=' + Date.now()
				 url:  '../index-list-detail/index-list-detail?item='+JSON.stringify(item)
			})
		},
	}
};
